import { createPortal } from "react-dom";
import { useEffect } from "react";
import PropTypes from "prop-types";

import styles from "./Modal.module.scss";

const modalRoot = document.querySelector("#modal-root");

const Modal = ({ children, onClose }) => {
  // Ð’ÐµÑˆÐ°ÐµÑ‚ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»Ð¸ (mount)
  useEffect(() => {
    window.addEventListener("keydown", handleKeyDown);

    // Ð£Ð±Ð¸Ñ€Ð°ÐµÑ‚ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÑ‚Ð¸ (unmount)
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  });

  // ÐÐ°Ð±Ð»ÑŽÐ´Ð°ÐµÑ‚ Ð½Ð° Escape Ð¸ Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ Ð¼Ð¾Ð´Ð°Ð»ÐºÑƒ
  const handleKeyDown = (event) => {
    if (event.code === "Escape") {
      onClose();
    }
  };

  // ÐÐ°Ð±Ð»ÑŽÐ´Ð°ÐµÑ‚ Ð·Ð° Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿Ð¾Ð¼ Ð¸ Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ Ð¼Ð¾Ð´Ð°Ð»ÐºÑƒ
  const handleBackdropClick = (event) => {
    if (event.currentTarget === event.target) {
      onClose();
    }
  };

  return createPortal(
    <div className={styles.Overlay} onClick={handleBackdropClick}>
      <div className={styles.Modal}>{children}</div>
    </div>,
    modalRoot
  );
};

Modal.defaultProps = {
  children: null,
};

Modal.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

export default Modal;
