import { useState } from "react";
import PropTypes from "prop-types";
import s from "./SearchForm.module.scss";

const SearchFrom = ({ onSearch }) => {
  const [query, setQuery] = useState("");

  // ÐÐ°Ð±Ð»ÑŽÐ´Ð°ÐµÑ‚ Ð·Ð° Ð¸Ð½Ð¿ÑƒÑ‚Ð¾Ð¼ Ð¸ Ð¿Ð¸ÑˆÐµÑ‚ Ð·Ð½Ð°Ñ‡Ð½Ð¸Ðµ Ð² ÑÑ‚ÐµÐ¹Ñ‚
  const handleSearchInput = (e) => {
    const { value } = e.currentTarget;

    setQuery(value);
  };

  // ÐÐ°Ð±Ð»ÑŽÐ´Ð°ÐµÑ‚ Ð·Ð° Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¾Ð¹ Ð¸ Ð¾Ñ‚Ð´Ð°ÐµÑ‚ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð²Ð¾ Ð²Ð½ÐµÑˆÐ½Ð¸Ð¹ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚
  const handleSubmit = (e) => {
    e.preventDefault();

    // Ð—Ð°Ð¿Ñ€ÐµÑ‰Ð°ÐµÑ‚ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÑƒ Ð¿ÑƒÑÑ‚Ð¾Ð³Ð¾ Ð¸Ð½Ð¿ÑƒÑ‚Ð°
    if (!query.trim()) return;

    // ÐžÑ‚Ð´Ð°Ñ‚ÑŒ Ð´Ð°Ð½Ð½Ñ‹Ðµ Ð²Ð½ÐµÑˆÐ½ÐµÐ¼Ñƒ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ñƒ
    onSearch(query);

    resetForm();
  };

  // Ð¡Ð±Ñ€Ð°ÑÑ‹Ð²Ð°ÐµÑ‚ Ð¿Ð¾Ð»Ðµ Ð¿Ð¾ÑÐ»Ðµ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¸
  const resetForm = () => setQuery("");

  return (
    <form className={s.SearchForm} onSubmit={handleSubmit}>
      <button type="submit" className={s["SearchForm-button"]}>
        <span className={s["SearchForm-button-label"]}>Search</span>
      </button>

      <input
        className={s["SearchForm-input"]}
        type="text"
        name="query"
        value={query}
        onChange={handleSearchInput}
        autoComplete="off"
        autoFocus
        placeholder="Search images and photos"
      />
    </form>
  );
};

SearchFrom.propTypes = {
  onSearch: PropTypes.func.isRequired,
};

export default SearchFrom;
